<template>
  <div>
    <el-container>
      <el-main>
        <el-row :gutter="0">
          <el-col :span="24" class="c_col">
            <el-input placeholder="输入视频播放地址" v-model="enterUrl" class="input-with-select" ref="inputEnter">
              <el-select v-model="selectUrlvlaue" slot="prepend" placeholder="请选择" class="ssss" style="width:165px;">
                <el-option v-for="item in selectUrl" :label="item.api_name" :value="item.api_url" :key="item.id">
                  {{item.api_name}}
                </el-option>
              </el-select>
              <el-button slot="append" @click="LookVideo">解析播放</el-button>
            </el-input>
          </el-col>
          <el-col :span="24" class="dpplayerdiv">
            <iframe :src="lookUrl" allowfullscreen="true" allowtransparency="true" id="player" frameborder="0" ></iframe>
          </el-col>
          <el-col :span="24" class="c_col">
            

          </el-col>
        </el-row>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import service from "../../utils/request";
export default {
  name: "other",
  data() {
    return {
      lookUrl:'',
      selectUrlvlaue:'',
      enterUrl:'',
      selectUrl: [],
    };
  },
  methods: {
    LookVideo() {
       
        if(this.selectUrlvlaue==''){
          // 默认选择
          this.selectUrlvlaue=this.selectUrl[0].api_url;
          // console.log(this.selectUrl[0].api_url);
          return ;
        }
        if(this.enterUrl==''){
          this.$message({ message: '请输入网址!',type: 'error'});//消息提示
          this.$refs.inputEnter.focus()
          return ;
        }else{
          this.lookUrl=this.selectUrlvlaue+this.enterUrl;
        }
        
    },
  },
  computed: {},
  mounted() {
    // 接口地址
    service
      .get("/api/video")
      .then((result) => {
        this.selectUrl = result.data;
        // console.log(result.data);
      })
      .catch((err) => {
        console.log(err);
      });
  },
};
</script>

<style scoped>
.el-container {
  width: 80%;
  margin: 0 auto;
}
.el-aside {
  margin-top: 20px;
}
.el-main {
  padding: 0px 10px 10px 0px;
  margin: 0;
}
#dplayer {
  margin-top: 15px;
  height: 580px;
  width: auto;
}
#player {
  height: 580px;
  width: 100%;
  background-color: black;
}
.select_{
  margin-right: 10px;
}
.c_col{
  margin-top: 10px;
  margin-bottom: 10px;
}
.el-select .el-input .input-with-select {
    width: 130px;
  }
  .ssss{
    /* border: 1px red solid; */
  }
</style>